import { StateData, BehaviorTreeBuilder, BehaviorTreeStatus, BehaviorTreeNodeInterface } from "fluent-behavior-tree";
import { Swarm } from "../game/swarm/Swarm";
import { UnitType, SwarmProperties } from "../game/model/SwarmConfigTypes";
import { SwarmBehaviour } from "../game/swarm/SwarmBehaviour";
import { Vector2D } from "../game/swarm/Vector2D";
import { MathHelper } from "../helper/MathHelper";
import { Unit } from "../game/swarm/Unit";

export class SwarmBehaviourTree{
    
    private builder:BehaviorTreeBuilder = new BehaviorTreeBuilder();
    private tree:BehaviorTreeNodeInterface;
    private swarm:Swarm;
    private swarms:Swarm[];

    private lastUpdateTime:number = 0;


    constructor(swarm:Swarm, swarms:Swarm[]){
        this.swarm = swarm;
        this.swarms = swarms;
        this.tree = this.createTree();
    }

    private createTree():BehaviorTreeNodeInterface{
        return new BehaviorTreeBuilder()
        //.sequence("my-parent-sequence")
        .selector("enemySelector")
        .splice(this.createAttackPlayerSubTree())
        .splice(this.createDefendFoodSubTree())
        .end()
        .build();
    }

    private createDefendFoodSubTree():BehaviorTreeNodeInterface{
        let nearestSwarm:Swarm;
        let lastSwarm:number = -1;
        let playerExists:boolean = SwarmBehaviour.getNearestSwarm(this.swarm, this.swarms, UnitType.PLAYER) != null;

        return new BehaviorTreeBuilder()
            .sequence("protectFood")
                .do("findFoodSwarm", async (t) =>{
                    if(this.swarm.properties.unitType == UnitType.ENEMY){
                        
                        // if there are player swarms -> fail early
                        if(playerExists){
                            return BehaviorTreeStatus.Failure;
                        }
                        
                        // find nearest food swarm
                        nearestSwarm = SwarmBehaviour.getNearestSwarm(this.swarm, this.swarms, UnitType.FOOD, lastSwarm);
                        
                        if(nearestSwarm){
                            lastSwarm = nearestSwarm.properties.id;   // only remember last
                            //ignoreSwarms.push(nearestSwarm.properties.id);
                            return BehaviorTreeStatus.Success;      // assigned target positions on circle for each unit
                        }
                        //console.log("no food swarm to protect");
                        return BehaviorTreeStatus.Failure;      // no food swarm found
                    }
                    //console.log("i am no enemy");
                    return BehaviorTreeStatus.Failure;
                })
                .do("assignPositions", async (t) => {
                    let radius:number = nearestSwarm.properties.spawnRadius + 20;
                    let positions:Array<Vector2D> = MathHelper.calculatePositionsOnCircle(this.swarm.units.length, radius);
                    let origin:Vector2D = nearestSwarm.properties.origin;
                    let counter:number = 0;
    
                    this.swarm.units.map((unit) => {
                        unit.targetPosition = positions[counter].add(origin);
                        counter++;
                    });
                    //console.log("assigned unit targetPositions");
                    return BehaviorTreeStatus.Success;
            })
            .do("seekTargetPositions", async (t) => {
                let distance:number;
                let numUnitsArrived:number = 0;
                let arrivalThreshold:number;

                if(playerExists){
                    return BehaviorTreeStatus.Failure;
                }

                this.swarm.units.map((unit) => {
                    // check distance for every unit to targetPosition
                    distance = unit.position.dist(unit.targetPosition);
                    arrivalThreshold = unit.arrivalThreshold;
                    arrivalThreshold = 5;

                    if(distance < arrivalThreshold){
                        numUnitsArrived++;
                    }
                });
                if(numUnitsArrived == this.swarm.units.length){
                    //console.log("enemy arrived at food defense positions");
                    return BehaviorTreeStatus.Success;
                }
                //console.log("seeking");
                return BehaviorTreeStatus.Running;
            })
            .end()
            .build();
    }


    
    private createAttackPlayerSubTree():BehaviorTreeNodeInterface{
        let potentialTarget:Swarm;
        let nearestSwarm:Swarm;
        let targetSwarmHasBeenReplaced:boolean = true;
        let lastTargetSwarmID:number = -1;

        return new BehaviorTreeBuilder()
            .sequence("attackPlayer")
                .do("findPlayerSwarm", async (t) =>{
                    //console.log("executing attackPlayer behavior");

                    //2. check every other swarm if it is food
                    if(this.swarm.properties.unitType == UnitType.ENEMY){       // only for enemy swarms
                        //console.log("i am an enemy looking for player");
                        nearestSwarm = SwarmBehaviour.getNearestSwarm(this.swarm, this.swarms, UnitType.PLAYER);
                        if(nearestSwarm){
                            this.swarm.properties.currentTargetSwarmLocation = nearestSwarm.properties.origin;
                            this.swarm.properties.targetSwarmID = nearestSwarm.properties.id;
                            
//                            console.log("found nearest player");
                            return BehaviorTreeStatus.Success;
                        }
                        return BehaviorTreeStatus.Failure;
                    }
                    //console.log("found not player");
                    return BehaviorTreeStatus.Failure;      // found no food swarm
                })
                .do("assignUnits", async (t) => {
                    this.swarm.units.map((unit) => {
                        if(!unit.targetUnit){
                            this.assignNewTarget(unit, nearestSwarm);
                            
                            
                        }
                    });
                   // console.log("assigned units");
                    return BehaviorTreeStatus.Success;
                })
                .do("seekTargetUnits", async (t) => {
                    // check success
                    if(nearestSwarm.properties.isAlive){
                        //console.log("player swarm still alive. seeking units");
                        this.swarm.units.map((unit) =>{
                            if(unit.targetUnit){
                                //console.log("seek");
                                unit.seek(unit.targetUnit.position);
                            }

                            if(this.swarm.checkUnitArrival(unit)){
                                this.assignNewTarget(unit, nearestSwarm);
                            }
                        });

                        if(this.swarm.hasArrived){
                            return BehaviorTreeStatus.Success;
                        }
                        else{
                            return BehaviorTreeStatus.Running;
                        }
                    }
                    else{
                        //console.log("all units killed");

                        this.swarm.properties.targetSwarmID = -1;   // forget about dead player swarm
                        return BehaviorTreeStatus.Success;
                    }
                })
                .end()
                .build();
    }

    private assignNewTarget(unit:Unit, swarm:Swarm):void{
        let targetUnit:Unit = swarm.getNextUntargetedUnit();

        if(targetUnit){
            unit.setTargetUnit( targetUnit );
            targetUnit.hasBeenTargeted = true;
        }
    }

    public clone():SwarmBehaviourTree{
        return new SwarmBehaviourTree(this.swarm, this.swarms);
    }

    public update(){
        let now:number = Date.now();
        let deltaTime:number = now - this.lastUpdateTime;
        this.lastUpdateTime = now;
        this.tree.tick(new StateData(deltaTime));

        this.swarm.units.map((unit) =>{
            unit.update();
        });
    }
}